.item {
  position: relative;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
.image {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn {
  position: absolute;
  top: 0;
  right: 10px;
  background-color: transparent;
  border: transparent;
  color: green;
  outline: transparent;
  &:hover {
    font-size: 25px;
    color: red;
  }
}
